                    </span>
                </div>
                <div class="cart__body-item__amount">
                    <span @click="changeCart('-',list.id)">-</span>
                    <p>{{ list.amount }}</p>
                    <span @click="changeCart('+',list.id)">+</span>
                </div>
                <p class="cart__body-item__price">{{ list.price * list.amount }} ‚ÇΩ</p>
                <img @click="delCart(list.id)" class="cart__body-item__close" src="@/assets/images/close.png" alt="">
            </div>
        </div>
        <div v-if="cart.length > 0" class="cart__end">
            <div class="cart__end-info">
                <p class="cart__end-info__all">–í—Å–µ–≥–æ –ø–∏—Ü—Ü: {{ cartLength }} —à—Ç.</p>
                <p class="cart__end-info__allPrice">–°—É–º–º–∞ –∑–∞–∫–∞–∑–∞: 900 ‚ÇΩ</p>
            </div>
            <div class="cart__end-buy">
                <router-link to="/pizza" class="cart__end-buy__home">
                    <img src="@/assets/images/left.svg" alt="">
                    –í–µ—Ä–Ω—É—Ç—å—Å—è –Ω–∞–∑–∞–¥
                </router-link>
                <router-link @click="buyAll" to="/pizza" class="cart__end-buy__buy">–û–ø–ª–∞—Ç–∏—Ç—å —Å–µ–π—á–∞—Å</router-link>
            </div>
    
        </div>
        <div v-else class="cart__empty">
            <h2 class="cart__empty-title">–ö–æ—Ä–∑–∏–Ω–∞ –ø—É—Å—Ç–∞—è üòï</h2>
            <p class="cart__empty-text">–í–µ—Ä–æ—è—Ç–Ω–µ–π –≤—Å–µ–≥–æ, –≤—ã –Ω–µ –∑–∞–∫–∞–∑—ã–≤–∞–ª–∏ –µ—â—ë –ø–∏—Ü—Ü—É. –î–ª—è —Ç–æ–≥–æ, —á—Ç–æ–±—ã –∑–∞–∫–∞–∑–∞—Ç—å –ø–∏—Ü—Ü—É, –ø–µ—Ä–µ–π–¥–∏ –Ω–∞ –≥–ª–∞–≤–Ω—É—é —Å—Ç—Ä–∞–Ω–∏—Ü—É.</p>
            <img src="../assets/images/pustoy.png" alt="">
            <router-link to="/pizza" class="cart__empty-btn">–í–µ—Ä–Ω—É—Ç—å—Å—è –Ω–∞–∑–∞–¥</router-link>
        </div>
    </div>
</template>

<script>
import { mapState,mapMutations,mapGetters } from "vuex";
export default {
data() {
    return {
        cartBool: true,
    }
},
computed:{
    ...mapState(["getUsers","cart"]),
    ...mapGetters(["cartLength"])
},
methods: {
    ...mapMutations(["cartChange","clearCart","clear","modalEdit"]),
    changeCart(val,id){
        let changeObj = {
            val: val,
            id: id
        }
        this.cartChange(changeObj)
    },
    delCart(id){
        this.clearCart(id)
    },
    clearAll(){
        this.clear()
    },
    buyAll(){
        this.modalEdit(true)
        setTimeout(() => {
            this.modalEdit(false)
        }, 3000);
        this.clear()
    }
},
}
</script>

